import React from 'react'
import "./CheckoutProduct.css"
import { useStateValue } from './StateProvider'
function CheckoutProduct({id,title,image,price,rating}) {
    const [{ cart },dispatch] = useStateValue();
//console.log(id,title,image,price,rating)
    const removeFromBasket=()=>{
        //remove  item from basket...
        dispatch({
            type:"REMOVE_FROM_BASKET",
            id:id,
        })
    }
    return (
        <div className="checkoutProduct">
        <img className="checkoutProduct_image" src={image} alt="gg" />
        <div className="checkoutProduct_info">
            <p className="checkoutProduct_title">{title}</p>

             <p className="checkoutProduct_price">
             <small>₹</small>
             <strong>{price}</strong></p>
             <div className="checkoutproduct_rating">
        {Array(rating)
        .fill()
        .map((_,i) => (
            <p>*</p>
        ))}

        </div> 
        <button style={{borderRadius:4,display:"",flexDirection:"",padding:5,margin:2,width:200,fontSize:18,fontFamily:"normal"}} onClick={removeFromBasket}>Remove From Cart</button>      
        </div>
            
        </div>
    )
}

export default CheckoutProduct
